const API_BASE = process.env.NEXT_PUBLIC_API_BASE || "http://localhost:8000";

async function request(path, options = {}) {
    const url = `${API_BASE}${path}`;
    const res = await fetch(url, {
        ...options,
        headers: {
            "Content-Type": "application/json",
            ...(options.headers || {}),
        },
        cache: "no-store",
    });
    if (!res.ok) {
        const text = await res.text();
        throw new Error(`API ${res.status} ${res.statusText}: ${text}`);
    }
    return res.json();
}

export async function fetchSegments() {
    return request("/api/segments");
}

export async function fetchDatathonForecast(road_id, timestamp) {
    return request("/api/datathon/forecast", {
        method: "POST",
        body: JSON.stringify({ road_id, timestamp }),
    });
}

export async function fetchHealth() {
    return request("/api/health");
}

export async function searchPredictions(payload) {
    return request("/api/search", {
        method: "POST",
        body: JSON.stringify(payload),
    });
}
